<template>
  <div id="touch-bar">
    <a>
      <img id="manageLayer" src="@/assets/image/tuceng.png" @click="openSideContent">
    </a>
    <a>
      <img id="manageLayer" src="@/assets/image/shezhi.png">
    </a>
    <a>
      <img id="manageLayer" src="@/assets/image/shezhi-2.png">
    </a>
  </div>
</template>
<script>

  export default {
    data () {
      return {
              
      }
    },
    methods: {
      openSideContent() {
        this.$emit('open-close')
      }         
    }
  }
</script>

<style scoped>
  #touch-bar {
    z-index: 1;
    width: 50px;
    height: 200px;
    display: -webkit-flex; /* Safari */
    -webkit-flex-flow: row-reverse wrap; /* Safari 6.1+ */
    display: flex;
    flex-flow: column;
    flex-wrap: wrap;

    /* 定位相关 */
    position: fixed;
    right: 5px;
    top: 70px;
  }
  #touch-bar img {
    margin: 5px;
    padding: 3px;
    background-color: azure;
    width: 35px;
    height: 35px;
  }
</style>



