import Mapbox from "./map.js"


function addLinkLayer(id,map,features,color,width) {
  map.addSource(id, {
    'type': "geojson",
    'data': {
        "type": "FeatureCollection",
        "features": features
  }  
 });
  map.addLayer({
    'id': id,
    'type': 'line',
    'source': id,
    'layout': {
      'line-join': 'round',
      'line-cap': 'round'
    },
    'paint': {
      'line-color': color,
      'line-width': width,
      'line-opacity': ["case",
      ["boolean", ["feature-state", "hover"], false],
      2,
      0.7
  ]
    }
  });  
  //点击图层显示弹窗popup
  Mapbox.addPopup('Link')
}

function addNodeLayer(id,map,features,color,radius) {
  map.addSource(id, {
    'type': "geojson",
    'data': {
        "type": "FeatureCollection",
        "features": features
  }  
 });
  map.addLayer({
    'id': id,
    'type': 'circle',
    'source': id,
    'paint': {
      'circle-radius': radius,
      'circle-color': color,
      'circle-opacity': ["case",
      ["boolean", ["feature-state", "hover"], false],
      2,
      0.7
  ]
    }
  });   
  //点击图层显示弹窗popup
  Mapbox.addPopup('Node')
}


export {
  addLinkLayer,
  addNodeLayer
}


