import XLSX from "xlsx"
import {
  toLineGeojson,
  toNodeGeojson
  } from "@/utils/transform.js"
import {
  addLinkLayer,
  addNodeLayer
  } from "@/utils/addLayer.js"

    // input读取本地excel文件(已弃用)
    function readWorkbookFromLocalFile(Map,features) {
      let that = this
      let resultFile = document.getElementById('file').files[0]
      console.log(resultFile)
      var reader = new FileReader();
      reader.readAsBinaryString(resultFile);
      // 读取文件,会触发 onload 异步事件,可使用回调函数 来获取最终的值.
      //注意这里的onload是异步事件
      reader.onload = function (e) {
        let fileContent = e.target.result
        var workbook = XLSX.read(fileContent, {type: 'binary'});
        console.log(workbook)
        let  worksheet = workbook.Sheets.Sheet1; // 这里我们只读取第一张sheet 
        //每一行的信息都会保存在json中
        var json = XLSX.utils.sheet_to_json(worksheet);
        console.log(json)
        switch(resultFile.name) {
          case "link.csv":
            features = toLineGeojson(json,features)
            //添加图层（注意要在异步函数onload里添加）
            addLinkLayer('Link',Map,features,'#adff2f',6); 
            break;
          case "node.csv":
            features = toNodeGeojson(json,features)
            //添加图层（注意要在异步函数onload里添加）
            addNodeLayer('Node',Map,features,'#d3d3d3',5); 
            break;                      

        }
      }
    }
    //elemnet_ui中读取本地文件方法
    function elReadFile(file,Map) {
      let that = this
      console.log(file)
      let resultFile = file
      console.log(resultFile)
      var reader = new FileReader();
      reader.readAsBinaryString(resultFile);
      // 读取文件,会触发 onload 异步事件,可使用回调函数 来获取最终的值.
      //注意这里的onload是异步事件
      reader.onload = function (e) {
        let fileContent = e.target.result
        var workbook = XLSX.read(fileContent, {type: 'binary'});
        console.log(workbook)
        let  worksheet = workbook.Sheets.Sheet1; // 这里我们只读取第一张sheet 
        //每一行的信息都会保存在json中
        var json = XLSX.utils.sheet_to_json(worksheet);
        console.log(json)
        switch(resultFile.name) {
          case "link.csv":
            var link_features = []
            link_features = toLineGeojson(json,link_features)
            //添加图层（注意要在异步函数onload里添加）
            console.log(link_features)
            addLinkLayer('Link',Map,link_features,'#adff2f',6); 
            //地图中心center飞跃
            var linkCenter = link_features[0].geometry.coordinates[0]
            console.log(linkCenter)
            Map.flyTo({
              center: linkCenter
              });
            break;
          case "node.csv":
            var node_features = []
            node_features = toNodeGeojson(json,node_features)
            //添加图层（注意要在异步函数onload里添加）
            console.log(node_features)
            addNodeLayer('Node',Map,node_features,'#d3d3d3',5); 
            //地图中心center飞跃
            var nodeCenter = node_features[0].geometry.coordinates
            console.log(nodeCenter)
            Map.flyTo({
              center: nodeCenter
              });            
            break;                      
        }
      }
    }

  export {
    readWorkbookFromLocalFile,
    elReadFile
  }


