<template>
  <div>
    <el-card class="box-card" shadow="always">
      <!-- <div class="text item">
        <input type="file" 
        id="file" 
        multiple="multiple"
        @change="input" />
      </div> -->
      <el-upload
        class="upload-demo"
        action="alert"
        :auto-upload="false"
        :on-change="handleChange"
        :file-list="fileList"
        :on-remove="removeFile">
        <el-button size="small" type="primary">Click upload</el-button>
        <div slot="tip" class="el-upload__tip">Only allow uploading of link.csv and node.csv files</div>
      </el-upload>
    </el-card>
  </div>
</template>
<script>

  export default {
    data () {
      return {   
        fileList: []           
      }
    },
    methods: {
      // input() {
      //   this.$emit('readFile')
      // },
      handleChange(file, fileList) {
        this.fileList = fileList.slice(-3);
        console.log(file)
        this.$emit('readFile',file.raw)
      },
      removeFile(file,fileList) {
        if(file.name==='node.csv') {
          this.map.map.removeLayer('Node')
          this.map.map.removeSource('Node')
        }else if(file.name==='link.csv') {
          this.map.map.removeLayer('Link')
          this.map.map.removeSource('Link')         
        }
      }
    }
  }
</script>

<style scoped>
  .el-card {
    border: rgba(37,39,47);
  }
  .box-card {
    width: 300px;
    /* height: 600px; */
    position: fixed;
    top:70px;
    left: 20px;
    bottom: 40px;
    z-index: 1;
    background-color: rgba(37,39,47);
  }
  .el-upload__tip {
    color: rgb(207, 200, 200);
  }
</style>



